import { LogedInUser } from "./authSlice"

export function createUser(userData): Promise<LogedInUser> {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/auth/signUp", {
        method: "POST",
        body: JSON.stringify(userData),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
}

export function logInUser(userData): Promise<LogedInUser> {
  return new Promise(async (resolve, reject) => {
   console.log(userData)
    try {
      const response = await fetch("/auth/login", {
        method: "POST",
        body: JSON.stringify(userData),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()

      console.log(data)
      if (response.ok) {
        resolve(data)
      } else {
        reject("user not found")
      }
    } catch (error) {
      reject("user not found")
    }
  })
}
