import CartPage from "./pages/cartPage"
import CheckoutPage from "./pages/checkoutPage"
import Home from "./pages/home"
import LoginPage from "./pages/loginPage"
import SignupPage from "./pages/signupPage"


import { createRoot } from "react-dom/client"
import {
  createBrowserRouter,
  RouterProvider,
  Route,
  Link,
} from "react-router-dom"
import ProductDetailPage from "./pages/productDetailPage"
import Protected from "./features/auth/components/protected"
import { useEffect } from "react"
import { useAppDispatch, useAppSelector } from "./app/hooks"
import { fetchUserAllCartAsync } from "./features/cart/cartSlice"
import { selectlogedInUser } from "./features/auth/authSlice"
import PageNotFound from "./pages/404page"
import OrderSuccessPage from "./pages/orderSuccess"
import UserOrdersPage from "./pages/userOrdersPage"
import UserProfilePage from "./pages/UserProfilePage"
import { fetchUserInfoAsync } from "./features/user/userSlice"
import AdminProtected from "./features/auth/components/adminProtected"
import AdminProductDetailPage from "./pages/adminProductDetailPage"
import AdminHome from "./pages/Adminhome"
import AdminProductFormPage from "./pages/adminProductFormPage"
import AdminOrderPage from "./pages/adminOrderPage"

const router = createBrowserRouter([
  {
    path: "/",
    element: (
      <Protected>
         <Home></Home>
      </Protected>
     
     
    ),
  },
  {
    path: "/admin",
    element: (
      <AdminProtected>
        <AdminHome></AdminHome>
      </AdminProtected>
    ),
  },
  {
    path: "/login",
    element: <LoginPage></LoginPage>,
  },
  {
    path: "/signup",
    element: <SignupPage></SignupPage>,
  },
  {
    path: "/cart",
    element: (
      <Protected>
        <CartPage></CartPage>
      </Protected>
    ),
  },
  {
    path: "/checkout",
    element: (
      <Protected>
        <CheckoutPage></CheckoutPage>
      </Protected>
    ),
  },
  {
    path: "/productDetail/:id",
    element: (
      <Protected>
        <ProductDetailPage></ProductDetailPage>
      </Protected>
      
    ),
  },

  {
    path: "admin/productDetail",
    element: (
      <AdminProtected>
        <AdminProductDetailPage></AdminProductDetailPage>
      </AdminProtected>
    ),
  },
  {
    path: "/admin/adminOrders",
    element: (
      <AdminProtected>
        <AdminOrderPage></AdminOrderPage>
      </AdminProtected>
    ),
  },

  {
    path: "/productform/edit/:id",
    element: (
      <AdminProtected>
        <AdminProductFormPage></AdminProductFormPage>
      </AdminProtected>
    ),
  },

  {
    path: "/productform",
    element: (
      <AdminProtected>
        <AdminProductFormPage></AdminProductFormPage>
      </AdminProtected>
    ),
  },

  {path : '/order-success/:id' , 
   element : (
    <Protected>
      <OrderSuccessPage></OrderSuccessPage>
    </Protected>
    
   )

  },

  {path : '/userOrders' , 
   element : (
    <Protected>
      <UserOrdersPage></UserOrdersPage>
    </Protected>
    
   )

  },

  {path : '/userProfile' , 
   element : (
    <Protected>
     <UserProfilePage></UserProfilePage>
    </Protected>
    
   )

  },

  {path : '*' , 
   element : (
    <PageNotFound></PageNotFound>
   )

  }

])

function App() {
  const dispatch = useAppDispatch()
  const User = useAppSelector(selectlogedInUser);


  useEffect(()=> {
    if(User.token !== ''){
      console.log(User.token)
      dispatch(fetchUserAllCartAsync(User.id))
      dispatch(fetchUserInfoAsync(User.id))
    }
  },[dispatch, User.token ])


  return (
    <div className="App">
      <RouterProvider router={router} />
    </div>
  )
}

export default App
