import { Order } from "../order/orderSlice";
import { UserInfo } from "./userSlice";

export function fetchUserOrders(id): Promise<Order[]> {
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/orders/user/"+id, {
        method: "GET",
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};

export function fetchUserInfo(id): Promise<UserInfo> {
  return new Promise(async (resolve, reject) => { 
    try {
      const response = await fetch(`/user/own/${id}`)
      const data = await response.json()
      resolve(data)
      console.log(data)
    } catch (error) {
      reject('user not found')
    }
  })
};

export function updateUser(updatedData): Promise<UserInfo>{
  return new Promise(async (resolve, reject) => {
    console.log( updatedData)
    try {
      const response = await fetch("/user/update/"+ updatedData.userId , {
        method: "PATCH",
        body: JSON.stringify(updatedData),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};
