import { useEffect } from "react"
import { useAppSelector, useAppDispatch } from "../../../app/hooks"
import { selectlogedInUser } from "../../auth/authSlice"
import { userOrdersAsync, selectUserOrders } from "../userSlice"
import { Link } from "react-router-dom"

export default function UserOrders() {
  const dispatch = useAppDispatch()
  const userOrders = useAppSelector(selectUserOrders)

  const User = useAppSelector(selectlogedInUser)

  useEffect(() => {
    dispatch(userOrdersAsync(User.id))
  }, [])

  if(userOrders.length > 0){
    console.log(userOrders)
  }

  return (
    <div className="mb-10" >
      <div className="mx-auto mt-5 max-w-7xl px-4 sm:px-6 lg:px-8">
      <p className="text-lg font-medium text-gray-900" >MY Orders</p>
      </div>
      
      {userOrders.map((order) => {
        return (
          <>
            <div className="mx-auto mt-5 max-w-7xl px-4 sm:px-6 lg:px-8">
              <div className="flex h-full flex-col  bg-white shadow-xl">
                <div className="flex-1  px-4 py-6 sm:px-6">
                  <div className="flex items-start justify-between">
                    <h4 className="text-lg font-medium text-gray-900">
                      Order No # {order.id}
                    </h4>
                    <h3 className="text-lg font-medium text-gray-900">
                      Order Status : {order.status}
                    </h3>
                  </div>
                  <div className="mt-8">
                    <div className="flow-root">
                      <ul
                        role="list"
                        className="-my-6 divide-y divide-gray-200"
                      >
                        {order.items.map((item) => (
                          <li key={item.id} className="flex py-6">
                            <div className="h-24 w-24 flex-shrink-0 overflow-hidden rounded-md border border-gray-200">
                              <img
                                src={item.product.images[0]}
                                alt={item.product.title}
                                className="h-full w-full object-cover object-center"
                              />
                            </div>

                            <div className="ml-4 flex flex-1 flex-col">
                              <div>
                                <div className="flex justify-between text-base font-medium text-gray-900">
                                  <h3>
                                    <a href={item.product.thumbnail}>
                                      {item.product.title}
                                    </a>
                                  </h3>
                                  <p className="ml-4">${item.product.price}</p>
                                </div>
                                <p className="mt-1 text-sm text-gray-500">
                                  {item.product.stock}
                                </p>
                              </div>
                              <div className="flex flex-1 items-end justify-between text-sm">
                                <div className="text-gray-500">
                                  <label
                                    htmlFor="quantity"
                                    className=" text-sm mr-5 inline font-medium leading-6 text-gray-900"
                                  >
                                    Qty : {item.quantity}
                                  </label>
                                  
                                </div>
                              </div>
                            </div>
                          </li>
                        ))}
                      </ul>
                    </div>
                  </div>
                </div>

                <div className="border-t border-gray-200 px-4 py-6 sm:px-6">
                  <div className="flex justify-between text-base font-medium text-gray-900">
                    <p>Subtotal</p>
                    <p>${order.totalAmount}</p>
                  </div>
                  <div className="flex justify-between text-base font-medium text-gray-900">
                    <p>Total items in Cart</p>
                    <p>{order.totalItems} items</p>
                  </div>
                  <p className="mt-0.5 text-sm text-gray-500">
                    Shipping and taxes calculated at checkout.
                  </p>
                  
                </div>
              </div>
            </div>
          </>
        )
      })}
    </div>
  )
}
