import { Cart } from "./cartSlice"

export function createCart(item): Promise<Cart>{
  return new Promise(async (resolve, reject) => {
    console.log(item)
    try {
      const response = await fetch("/cart", {
        method: "POST",
        body: JSON.stringify(item),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};

export function updateCart(updateitem): Promise<Cart>{
  return new Promise(async (resolve, reject) => {
    console.log( updateitem)
    try {
      const response = await fetch(`/cart/update/${updateitem.id}` , {
        method: "PATCH",
        body: JSON.stringify(updateitem),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};

export function deleteCart(id): Promise<number> {
  return new Promise(async (resolve, reject) => {
    console.log(id)
    try {
      const response = await fetch(`/cart/delete/${id}`, {
        method: "DELETE",
        headers: { "content-type": "application/json" },
      });

      if (!response.ok) {
        // Check for HTTP error status
        const errorMessage = await response.text();
        reject(`Error: ${response.status} - ${errorMessage}`);
        return;
      }

      const data = await response.json();
      resolve(id);
    } catch (error) {
      reject(`Error: ${error.message}`);
    }
  });
}

export function fetchUserAllCart(id): Promise<Cart[]>{
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetch("/cart/user/"+id)
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};

export function resetCart(id): Promise<string>{
  return new Promise(async (resolve, reject) => {
    try {
      const response = await fetchUserAllCart(id)
      const items = response.map((item) => item.id)
      for(let itemID of items){
        console.log(itemID)
        if(itemID !== null){
          await deleteCart(itemID)
        }
        
      } 
      resolve('success')
    } catch (error) {
      reject(error)
    }
  })
};

