import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState, AppThunk } from "../../app/store"
import { fetchAllProducts, fetchAllFilterProducts, fetchAllCategories, fetchAllBrands, fetchSingleProduct, createProduct, updateProduct } from "./productAPI"

export interface Product {
  id: string;
  title: string;
  description : string;
  price: number;
  discountPercentage: number;
  rating: number;
  stock: number;
  brand: string;
  category: string;
  deleted: boolean;
  thumbnail: string;
  images: string[]

}

export interface Category {
  value: string;
  label: string;
  checked : boolean;

}

export interface Brand {
  value: string;
  label: string;
  checked : boolean;

}

export interface CounterState {
  product: Product
  brands: Brand[]
  categories: Category[]
  totalItems: number,
  products: Product[]
  status: "idle" | "loading" | "failed"
}

const initialState: CounterState = {
  product: null,
  brands: [],
  categories: [],
  totalItems:0,
  products: [],
  status: "idle",
}

export const fetchAllProductsAsync = createAsyncThunk<Product[]>(
  "product/fetchAllProducts",
  async () => {
    try {
      const data = await fetchAllProducts();
      console.log(data);
      return data;
    } catch (error) {
      console.error('Error fetching products:', error);
      throw error; 
    }
  }
);

export const fetchSingleProductAsync = createAsyncThunk<Product, string>(
  "product/fetchSingleProduct",
  async (id) => {
    try {
      const data = await fetchSingleProduct(id);
      console.log(data);
      return data;
    } catch (error) {
      console.error('Error fetching products:', error);
      throw error; 
    }
  }
);

export const updateProductAsync = createAsyncThunk<Product, {} >(
  "product/updateProduct",
  async (updateItem) => {
    const data = await updateProduct(updateItem)
    return data
  },
)

export const createProductAsync = createAsyncThunk<Product, {}>(
  "product/createProduct",
  async (productData) => {
    try {
      const data = await createProduct(productData);
      console.log(data);
      return data;
    } catch (error) {
      console.error('Error fetching products:', error);
      throw error; 
    }
  }
)

export const fetchAllCategoriesAsync = createAsyncThunk<Category[]>(
  "categories/fetchAllCategories",
 
  async () => {
    
    try{
      const data = await fetchAllCategories();
      console.log('categories => ', data)
      return data
    } catch (error) {
      console.error('Error fetching categories:', error);
      throw error
    }
  }
)

export const fetchAllBrandsAsync = createAsyncThunk<Category[]>(
  "categories/fetchAllBrands",
 
  async () => {
    
    try{
      const data = await fetchAllBrands();
      console.log('categories => ', data)
      return data
    } catch (error) {
      console.error('Error fetching categories:', error);
      throw error
    }
  }
)

export const fetchAllFilterProductsAsync = createAsyncThunk<
  { products: Product[]; totalItems: number },
  { filter: { [key: string]: string }; sort: { [key: string]: string }; pagination: { [key: number]: number } }
>(
  "product/fetchAllFilterProducts",
  async ({ filter, sort, pagination }) => {
    try {
      const { products, totalItems } = await fetchAllFilterProducts(filter, sort, pagination);
      console.log(products);

      // Assuming fetchAllFilterProducts returns Product[]
      return { products, totalItems }; // Fix: Provide totalItems property
    } catch (error) {
      console.error('Error fetching filtered products:', error);
      throw error; // Re-throw the error to let Redux Toolkit handle it
    }
  }
);


export const productSlice = createSlice({
  name: "product",
  initialState,

  reducers: {
    setProduct: (state) => {
     
      state.product = null
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchAllProductsAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchAllProductsAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.products = action.payload
      })
      .addCase(fetchAllProductsAsync.rejected, (state) => {
        state.status = "failed"
      })
      .addCase(fetchAllFilterProductsAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchAllFilterProductsAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.products = action.payload.products;
        state.totalItems = action.payload.totalItems;
      })
      .addCase(fetchAllFilterProductsAsync.rejected, (state) => {
        state.status = "failed"
      })
      .addCase(fetchAllCategoriesAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchAllCategoriesAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.categories = action.payload;
        console.log(state.categories)
      })
      .addCase(fetchAllBrandsAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchAllBrandsAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.brands = action.payload;
        console.log(state.brands)
      })
      .addCase(fetchSingleProductAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchSingleProductAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.product = action.payload;
        console.log(state.brands)
      })
      .addCase(createProductAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(createProductAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.products.push(action.payload);
        
      })
      .addCase(updateProductAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(updateProductAsync.fulfilled, (state, action) => {
        state.status = "idle"
        const index = state.products.findIndex(item => item.id === action.payload.id)
        state.products[index] = action.payload
        
      })
      
  },
})

export const { setProduct } = productSlice.actions

export const selectAllProducts = (state: RootState) => state.product.products
export const selectTotalItems = (state) => state.product.totalItems;
export const selectAllCategories = (state) => state.product.categories;
export const selectAllBrands = (state) => state.product.brands;
export const selectProduct = (state) => state.product.product;

export default productSlice.reducer
