import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState, AppThunk } from "../../app/store"
import { createCart, fetchUserAllCart, updateCart, deleteCart, resetCart } from "./cartAPI"
import { UserInfo } from "../user/userSlice";

export interface Product {
  id: number;
  title: string;
  description : string;
  price: number;
  discountPercentage: number;
  rating: number;
  stock: number;
  brand: string;
  category: string;
  thumbnail: string;
  quantity:number
  userId: number
  productId: number
  images: string[]

}

export interface Cart {
  id: number
  quantity: number,
  product: Product,
  user: UserInfo
}

export interface CartState {
  items: Cart[]
  status: "idle" | "loading" | "failed"
}

const initialState: CartState = {
  items: [],
  status: "idle",
}

export const createCartAsync = createAsyncThunk<Cart, {} >(
  "cart/createCart",
  async (item) => {
    const data = await createCart(item)
    return data
  },
);


export const fetchUserAllCartAsync = createAsyncThunk<Cart[], string >(
  "cart/fetchUserAllCart",
  async (id) => {
    const data = await fetchUserAllCart(id)
    return data
  },
)

export const resetCartAsync = createAsyncThunk<String, String >(
  "cart/resetCartAsync",
  async (id) => {
    const data = await resetCart(id)
    return data
  },
)

export const updateCartItemAsync = createAsyncThunk<Cart, {} >(
  "cart/updateCart",
  async (updateItem) => {
    const data = await updateCart(updateItem)
    return data
  },
)

export const deleteCartItemAsync = createAsyncThunk<number, number>(
  "cart/deleteCart",
  async (id) => {
    console.log(id)
    const data = await deleteCart(id)
    return data
  },
)



export const cartSlice = createSlice({
  name: "cart",
  initialState,

  reducers: {
    increment: (state) => {
     
      // state.value += 1
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(createCartAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(createCartAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.items.push(action.payload)
      })
      .addCase(createCartAsync.rejected, (state) => {
        state.status = "failed"
      })
      .addCase(fetchUserAllCartAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchUserAllCartAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.items = action.payload
      })
      .addCase(updateCartItemAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(updateCartItemAsync.fulfilled, (state, action) => {
        state.status = "idle"
        const index = state.items.findIndex(item => item.id === action.payload.id)
        state.items[index] = action.payload
        console.log(state.items)
      })
      .addCase(deleteCartItemAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(deleteCartItemAsync.fulfilled, (state, action) => {
        state.status = "idle"
        const index = state.items.findIndex(item => item.id === action.payload)
        state.items.splice(index, 1);
      })
      .addCase(resetCartAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(resetCartAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.items = []
      })
  },
})

export const { increment } = cartSlice.actions

export const selectItems = (state: RootState) => state.cart.items

export default cartSlice.reducer
