import { Link, Navigate, useParams } from "react-router-dom";
import { useAppSelector, useAppDispatch } from "../app/hooks";
import { currentOrderEmpty } from "../features/order/orderSlice";
import { resetCartAsync } from "../features/cart/cartSlice";
import { selectlogedInUser } from "../features/auth/authSlice";
import { useEffect } from "react";

export default function OrderSuccessPage() {
  const params = useParams()
  const dispatch = useAppDispatch()
  const User = useAppSelector(selectlogedInUser)

  const handleClick =() => {
    dispatch(currentOrderEmpty())
  }

  useEffect(() => {
    dispatch(resetCartAsync(User.id))
  }, [dispatch])
  return [
    <>
      {!params.id && <Navigate to='/' replace={true}></Navigate>}
      <main className="grid min-h-full place-items-center bg-white px-6 py-24 sm:py-32 lg:px-8">
        <div className="text-center">
          <p className="text-base font-semibold text-indigo-600">order Successfull</p>
          <h1 className="mt-4 text-3xl font-bold tracking-tight text-gray-900 sm:text-5xl">
            Order id #### {params?.id} successfull
          </h1>
          <p className="mt-6 text-base leading-7 text-gray-600">
           you can find your orders here
          </p>
          <div className="mt-10 flex items-center justify-center gap-x-6">
            <Link
              to={'/'}
              onClick={handleClick}
              className="rounded-md bg-indigo-600 px-3.5 py-2.5 text-sm font-semibold text-white shadow-sm hover:bg-indigo-500 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600"
            >
              Go back home
            </Link>
            <a href="#" className="text-sm font-semibold text-gray-900">
              Contact support <span aria-hidden="true">&rarr;</span>
            </a>
          </div>
        </div>
      </main>
    </>,
  ]
}
