import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState, AppThunk } from "../../app/store"
import { fetchUserInfo, fetchUserOrders, updateUser } from "./userAPI"
import { Order } from "../order/orderSlice"
import { LogedInUser } from "../auth/authSlice"

export interface Address {
  fullname: string
  email: string
  phone: number
  street: string
  city: string
  region: string
  pincode: string
}

export interface UserInfo {
  id: number
  email: string
  password: string
  addresses: Address[]
  role:string
}

export interface UserState {
  UserInfo: UserInfo
  orders: Order[]
  status: "idle" | "loading" | "failed"
  error: String
}

const initialState: UserState = {
  UserInfo: {
    id: null,
    email: "",
    password: "",
    addresses: [],
    role:''
  },
  orders: [],
  status: "idle",
  error: "",
}

export const fetchUserInfoAsync = createAsyncThunk<UserInfo, string>(
  "user/fetchUserInfo",
  async (id) => {
    const data = await fetchUserInfo(id)
    return data
  },
)

export const userOrdersAsync = createAsyncThunk<Order[], string>(
  "user/fetchUserOrders",
  async (id) => {
    const data = await fetchUserOrders(id)
    return data
  },
)

export const updateUserInfoAsync = createAsyncThunk<
UserInfo,
  { userId: number; addresses: Partial<Address>[] }
>("user/updateUserInfo", async (userData) => {
  const data = await updateUser(userData)
  return data
})

export const counterSlice = createSlice({
  name: "counter",
  initialState,

  reducers: {
    increment: (state) => {
      // state.value += 1
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(userOrdersAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(userOrdersAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.orders = action.payload
      })
      .addCase(userOrdersAsync.rejected, (state) => {
        state.status = "failed"
      })
      .addCase(fetchUserInfoAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchUserInfoAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.UserInfo = action.payload
      })
      .addCase(updateUserInfoAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.UserInfo = action.payload
      })
      .addCase(updateUserInfoAsync.rejected, (state, action) => {
        state.status = "failed"
        state.error = action.error.message || "An error occurred"
      })
  },
})

export const { increment } = counterSlice.actions

export const selectUserOrders = (state: RootState) => state.user.orders
export const selectUserInfo = (state: RootState) => state.user.UserInfo

export default counterSlice.reducer
