import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState, AppThunk } from "../../app/store"
import { createOrder, fetchAllOrders, updateOrder } from "./orderAPI"
import { Cart, Product } from "../cart/cartSlice"
import { LogedInUser } from "../auth/authSlice"
import { Address, UserInfo } from "../user/userSlice"

export interface Order {
  id:number
  items: Cart[]
  user: UserInfo
  totalAmount: number
  totalItems: number
  paymentMethod: string
  selectedAddress: Address
  status: "pending" | "fulfilled" | "failed"
}

export interface OrderState {
  orders: Order[]
  status: "idle" | "loading" | "failed"
  currentOrder: Order
  totalItems: number
}

const initialState: OrderState = {
  orders: [],
  status: "idle",
  currentOrder: null,
  totalItems: 0
}

export const createOrderAsync = createAsyncThunk<Order, {} >(
  "order/createOrder",
  async (orderData) => {
    const data = await createOrder(orderData)
    return data
  },
);


export const updateOrderAsync = createAsyncThunk<Order, {} >(
  "order/updateOrder",
  async (orderData) => {
    const data = await updateOrder(orderData)
    return data
  },
);


// sort: { [key: string]: string }; pagination: { [key: number]: number }
// {sort, pagination }

export const fetchAllOrdersAsync = createAsyncThunk<
  { orders: Order[]; totalItems: number },
  {sort: { [key: string]: string }; pagination: { [key: number]: number }}
>(
  "order/fetchAllOrders",
  async ({sort, pagination }) => {
    try {
      const { orders, totalItems } = await fetchAllOrders(sort, pagination);
      console.log(orders);

      // Assuming fetchAllFilterProducts returns Product[]
      return { orders, totalItems }; // Fix: Provide totalItems property
    } catch (error) {
      console.error('Error fetching filtered products:', error);
      throw error; // Re-throw the error to let Redux Toolkit handle it
    }
  }
);



export const orderSlice = createSlice({
  name: "order",
  initialState,

  reducers: {
    currentOrderEmpty: (state) => {
     
      state.currentOrder = null
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(createOrderAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(createOrderAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.orders.push(action.payload)
        state.currentOrder = action.payload
      })
      .addCase(createOrderAsync.rejected, (state) => {
        state.status = "failed"
      })
      .addCase(fetchAllOrdersAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(fetchAllOrdersAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.orders = action.payload.orders;
        state.totalItems = action.payload.totalItems;
      })
      .addCase(updateOrderAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(updateOrderAsync.fulfilled, (state, action) => {
        state.status = "idle"
        const index = state.orders.findIndex(item => item.id === action.payload.id)
        state.orders[index] = action.payload
      })
  },
})

export const { currentOrderEmpty } = orderSlice.actions

export const selectOrders = (state: RootState) => state.order.orders
export const selectcurrentOrder = (state: RootState) => state.order.currentOrder
export const selectTotalItems = (state) => state.order.totalItems;

export default orderSlice.reducer
