import { Order } from "./orderSlice"

export function fetchAllOrders(sort, pagination): Promise<{ orders: Order[]; totalItems: number }> {

  let queryString = ""
    for(let key in sort){
      queryString += `${key}=${sort[key]}&`
    }
    console.log(pagination)
    for(let key in pagination){
      queryString += `${key}=${pagination[key]}&`
    }

  return new Promise(async (resolve, reject) => {
    
    try {
      const response = await fetch(
        "/orders?" + queryString,
      )
      if (!response.ok) {
        // Handle non-successful responses
        throw new Error(`HTTP error! Status: ${response.status}`)
      }

      const data = await response.json()
      const totalItems = await response.headers.get('X-Total-Count')
      console.log(data)
      resolve({ orders: data, totalItems: +totalItems });
    } catch (error) {
      reject(error)
    }
  })
};




export function createOrder(orderData): Promise<Order> {
  return new Promise(async (resolve, reject) => {
    console.log(orderData)
    try {
      const response = await fetch("/orders/create", {
        method: "POST",
        body: JSON.stringify(orderData),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};

export function updateOrder(orderData): Promise<Order> {
  return new Promise(async (resolve, reject) => {
    console.log(orderData)
    try {
      const response = await fetch("/orders/update/"+ orderData.id, {
        method: "PATCH",
        body: JSON.stringify(orderData),
        headers: { "content-type": "application/json" },
      })
      const data = await response.json()
      resolve(data)
    } catch (error) {
      reject(error)
    }
  })
};

