import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit"
import { RootState, AppThunk } from "../../app/store"
import { createUser, logInUser} from "./authApi"


export interface LogedInUser {
   token: string;
   id:string
}

export interface UserState {
  logedInuser: LogedInUser
  status: "idle" | "loading" | "failed"
  error: string
}

const initialState: UserState = {
  logedInuser: {
    token: '',
    id: ''
  },
  status: "idle",
  error: ''
}

export const createUserAsync = createAsyncThunk<LogedInUser, {email : string, password: string, addresses: [], role: string}>(
  "user/createUser",
  async (userData) => {
    const data = await createUser(userData)
    return data
  },
);

export const logInUserAsync = createAsyncThunk<LogedInUser, {email : string, password: string}>(
  "user/findUser",
  async (userData) => {
    const data = await logInUser(userData)
    return data 
  },
);



export const userSlice = createSlice({
  name: "user",
  initialState,

  reducers: {
    logout: (state) => {
     
      state.logedInuser.token = "";
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(createUserAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(createUserAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.logedInuser = action.payload
        console.log(state.logedInuser)
      })
      .addCase(createUserAsync.rejected, (state) => {
        state.status = "failed"
      })
      .addCase(logInUserAsync.pending, (state) => {
        state.status = "loading"
      })
      .addCase(logInUserAsync.fulfilled, (state, action) => {
        state.status = "idle"
        state.logedInuser = action.payload
        console.log(state.logedInuser)
      })
      .addCase(logInUserAsync.rejected, (state, action) => {
        state.status = "failed"
        state.error = action.error.message || "An error occurred";
      })
      
  },
})

export const { logout } = userSlice.actions

export const selectlogedInUser = (state: RootState) => state.auth.logedInuser
export const selectError = (state: RootState) => state.auth.error

export default userSlice.reducer
